<template>
	<div class="icons">
		<swiper :options="swiperOptions">
			<swiper-slide v-for="(page,index) of pages" :key="index">
				<router-link class="icon" v-for="item of page" :key="item.id" :to="'/detail/'+item.id">
					<div class="icon-img">
						<img class="icon-img-content" :src="item.imgUrl">
					</div>
					<p class="icon-desc">{{item.desc}}</p>
				</router-link>
			</swiper-slide>
		</swiper>
	</div>
</template>

<script>
export default{
	name:'HomeIcons',
	props:{
		list:Array
	},
	data () {
		return {
			swiperOptions:{
				autoplay:false //禁止轮播自动滚动
			}
		}
	},
	computed:{
		pages(){
			const pages = []
			this.list.forEach((item,index) => {
				const page =Math.floor(index / 8)
				if (!pages[page]){
					pages[page] = []
				}
				pages[page].push(item)
			})
			return pages;
		}
	}
}
</script>

