<template>
	<div>
		<div class="banner" @click="handleBannerClick">
			<img class="banner-img" :src="bannerImg" alt="">
			<div class="banner-info">
				<div class="banner-tittle">
				{{this.sightName}}
				</div>
				<div class="banner-number">
					<span class="iconfont banner-icon">&#xe6de;</span>
					{{this.bannerImgs.length}}
				</div>
			</div>
		</div>
		<fade-animation>
			<common-gallary :imgs="bannerImgs" v-show="showGallay" @close="handleGallaryClose"></common-gallary>
		</fade-animation>
	</div>
</template>

<script>
import CommonGallary from 'common/gallary/Gallary'
import FadeAnimation from 'common/fade/FadeAnimation'
export default{
	name:'DetailBanner',
	props:{
		sightName:String,
		bannerImg:String,
		bannerImgs:Array
	},
	//逻辑设置
	data (){
	    return{
	        //图片画廊默认隐藏
	        showGallay:false       
	    }
	},
	methods:{
	    //点击banner显示图片画廊
	    handleBannerClick () {
	        this.showGallay = true
	    },
	    //点击图片画廊空处隐藏
	    handleGallaryClose(){
	        this.showGallay = false
	    }
	},
	components:{
		CommonGallary,
		FadeAnimation
	}
}
</script>

